<template>
    <div class="serach">
        <Header :isLeft='true' title='搜索' />
        <div class="search_header">
            <form action="" class="search_wrap">
                <i class="fa fa-search"></i>
                <input type="text" v-model="key_word" placeholder="输入商家,商品信息">
                <button @click.prevent='searchHandle'>搜索</button>
            </form>
        </div>
        <div class="shop" v-if="result && !showShop">
            <div class="empty_wrap" v-if="empty">
                <img src="https://fuss10.elemecdn.com/d/60/70008646170d1f654e926a2aaa3afpng.png" alt="">
                <div class="empty_txt">
                    <h4>附近没有搜索结果</h4>
                    <span>换个关键词试试吧</span>
                </div>
            </div>
            <div v-else>
                <SearchIndex @click='$router.push("/shop")' :data='result.restaurants' />
                <SearchIndex @click='shopItemClick' :data='result.words' />
            </div>
        </div>
        <div class="container" v-if="showShop">
            <!-- 导航 -->
            <FilterView class='aa' :filterData='filterData' @update='update' />
            <div class="shoplist" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading">
                <IndexShop v-for="(item,index) in restaurants" :key="index" :restaurant='item.restaurant'/>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        ref,
        watch,
        onMounted
    } from '@vue/composition-api'
    import Header from '../components/Header'
    import axios from 'axios'
    import SearchIndex from '../components/SearchIndex'
    import FilterView from '../components/FilterView'
    import IndexShop from '../components/IndexShop'
    import { InfiniteScroll } from 'mint-ui';
    export default {
        name: 'Search',
        components: {
            Header,
            SearchIndex,
            FilterView,
            IndexShop
        },
        setup(props, {
            root,
            refs
        }) {
            const key_word = ref('')
            const result = ref(null)
            const empty = ref(false)
            const showShop = ref(false)
            const filterData=ref(null)
            const dataa = ref(null)
            const restaurants=ref([])
            const page = ref(0)
            const size = ref(7)
            const loading= ref(false)
            watch(() => key_word.value, (value) => {
                // console.log(value);
                empty.value = false
                showShop.value = false
                axios.get(`/api/profile/typeahead/${key_word.value}`).then(res => {
                    // console.log(res.data);
                    result.value = res.data
                }).catch(err => {
                    result.value = null
                })
            })

            const searchHandle = () => {
                if (!key_word.value) return
                //搜索
                if (result.value && (result.value.restaurants.length > 0 || result.value.words.length)) {
                    // console.log('有内容');
                    shopItemClick()
                } else {
                    empty.value = true
                }
            }

            const shopItemClick = () => {
                page.value =0
                restaurants.value =[]
                showShop.value = true
            }

            const update=(condition)=> {
                // console.log(condition);
                dataa.value=condition
                shopItemClick()
            }

            const loadMore=()=>{
                console.log(1);
                page.value++;
                axios.post(`/api/profile/restaurants/${page.value}/${size.value}`,dataa).then(res=>{
                    // console.log(res.data);
                    // refs.loadmore.onTopLoaded()
                    // restaurants.value =res.data
                    // console.log(restaurants);
                    if(res.data.length>0){
                        res.data.forEach(item=>{
                            restaurants.value.push(item)
                        })
                    }else{
                        loading.value=true
                    }
                })
            }

            onMounted(() => {
                axios.get('/api/profile/filter').then(res=>{
                    // console.log(res.data);
                    filterData.value =res.data
                    // console.log(filterData.value);
                    
                })
                // console.log(restaurants.value);
            })

            return {
                key_word,
                result,
                empty,
                searchHandle,
                showShop,
                shopItemClick,
                filterData,
                update,
                restaurants,
                page,
                size,
                loadMore
            }
        }
    }
</script>

<style scoped>
    .search {
        width: 100%;
        height: 100%;
        overflow: auto;
        box-sizing: border-box;
    }

    .search_header {
        margin-top: 45px;
        background: #fff;
        padding: 0 4.266667vw;
    }

    .search_header .search_wrap {
        padding: 2.933333vw 2.933333vw 2.933333vw 0;
        display: flex;
        align-items: center;
        position: relative;
    }

    .search_wrap .fa-search {
        width: 2.933333vw;
        height: 2.933333vw;
        color: #888;
        position: absolute;
        top: 4.6666666vw;
        left: 2.933333vw;
    }

    .search_wrap input {
        flex-grow: 1;
        border-radius: 0.266667vw;
        background-color: #f8f8f8;
        padding: 1.733333vw 4vw 1.733333vw 8.8vw;
        color: #666;
        outline: none;
        border: none;
    }

    .search_wrap button {
        outline: none;
        border: none;
        color: #333;
        font-size: 0.426667rem;
        background: #fff;
        font-weight: 700;
        margin-left: 2.933333vw;
        font-size: 14px;
    }

    .shop {
        width: 100%;
        height: calc(100% - 95px);
        overflow: auto;
    }

    .empty_wrap {
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: #fff;
        display: flex;
        justify-content: center;
    }

    .empty_wrap img {
        width: 35vw;
        height: 35vw;
    }

    .empty_txt h4 {
        color: #666;
        font-size: 1rem;
        margin: 12vw 0 2vw 0;
    }

    .empty_txt span {
        color: #999;
        font-size: 0.8rem;
    }
</style>